.pricing {
    display: flex;
    justify-content: center;
    align-items: center;

    &__text {
        text-align: center;
        margin-top: 150px;
        margin-bottom: 100px;
        font-size: 32px;
        letter-spacing: 1.5px;
    }

    &__box--basic {
        position: relative;
        float: left;
        text-align: center;
        // background: #FFFFFF 0% 0% no-repeat padding-box;
        border: 1px solid #FB3B64;
        border-radius: 18px;
        opacity: 1;
        width: 330px;
        height: 430px;
        margin: 50px 20px;
        transform-style: preserve-3d;
        transition: all 0.7s ease-in-out;

        &:hover {
            transform: rotateY(180deg);
            cursor: pointer;
        }

        &--front {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            backface-visibility: hidden;
            width: 100%;
            height: 100%;
            background: #FB3B64;
            border-radius: 15px;

            span {
                font-size: 20px;
                font-weight: 700;
                letter-spacing: 1px;
                color: black;
            }
        }

        &--back {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            backface-visibility: hidden;
            transform: rotateY(180deg);
            width: 100%;
            height: 100%;
            box-shadow: 5px 10px 27px #FB3B64;
            border-radius: 17px;

            &--list--price {
                font-size: 30px;
                color: #FB3B64;
            }

            h1 {
                margin-top: 30px;
                font-size: 20px;
                font-weight: 700;
                letter-spacing: 1px;
            }

            &--list li {
                border: none;
                list-style: none;
                margin-top: 15px;
                padding: 5px 0px;

                img {
                    margin: 0 10px;
                }
            }

            &--btn {
                width: 170px;
                height: 40px;
                border-radius: 25px;
                background-color: #FB3B64;
                border: none;
                color: white;
                margin-top: 53px;
            }
        }
    }

